// ==========================================
// 1. VARIABLES
// ==========================================
$principal-color: #007bff;
$second-color: #6c63ff;

$light-text: #fff;
$dark-text: #222;
$muted-text: #666;

$dark-bg-overlay: rgba(0, 0, 0, 0.6);
$light-bg: rgba(255, 255, 255, 0.15); // effet "glass"
$border-radius: 14px;
$transition-speed: 0.35s;
$fast-transition: all $transition-speed ease-in-out;
$blur-effect: blur(12px);
$shadow-soft: 0 10px 35px rgba(0, 0, 0, 0.25);
$shadow-hover: 0 15px 45px rgba(0, 0, 0, 0.35);

// ==========================================
// 2. MIXINS
// ==========================================
@mixin modern-button($bg-color, $shadow-color) {
  padding: 10px 24px;
  border-radius: $border-radius;
  text-decoration: none;
  font-weight: 600;
  cursor: pointer;
  transition: $fast-transition;
  color: $light-text;
  background: linear-gradient(135deg, lighten($bg-color, 10%), darken($bg-color, 5%));
  border: none;
  box-shadow: 0 4px 15px rgba($shadow-color, 0.3);
  letter-spacing: 0.5px;

  &:hover {
    transform: translateY(-3px) scale(1.03);
    filter: brightness(1.1);
    box-shadow: 0 6px 25px rgba($shadow-color, 0.45);
  }

  &:active {
    transform: scale(0.97);
  }
}

// ==========================================
// 3. MODALE OVERLAY
// ==========================================
.modal-overlay {
  position: fixed;
  inset: 0;
  background: linear-gradient(135deg, rgba(0, 0, 0, 0.7), rgba(30, 30, 30, 0.8));
  backdrop-filter: $blur-effect;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;

  visibility: hidden;
  opacity: 0;
  transition: opacity $transition-speed ease, visibility $transition-speed;

  &.is-visible {
    visibility: visible;
    opacity: 1;
    animation: fadeIn 0.5s ease forwards;
  }
}

// ==========================================
// 4. MODALE CONTENT
// ==========================================
.modal-content {
  background: $light-bg;
  backdrop-filter: blur(20px) saturate(180%);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  padding: 40px;
  border-radius: $border-radius;
  max-width: 700px;
  width: 90%;
  color: $dark-text;
  box-shadow: $shadow-soft;
  position: relative;
  overflow-y: auto;
  max-height: 90vh;

  // Animation d'apparition
  opacity: 0;
  transform: translateY(20px) scale(0.96);
  transition: transform 0.4s ease, opacity 0.4s ease;

  .modal-overlay.is-visible & {
    opacity: 1;
    transform: translateY(0) scale(1);
  }

  // ===== HEADER =====
  .modal-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid rgba(255, 255, 255, 0.1);
    padding-bottom: 18px;
    margin-bottom: 25px;

    h3 {
      color: $principal-color;
      font-weight: 700;
      font-size: 1.8rem;
      margin: 0;
      text-transform: uppercase;
      letter-spacing: 1px;
    }

    .sourceCode .button {
      @include modern-button($principal-color, $principal-color);
      font-size: 0.95rem;
    }
  }

  // ===== BODY =====
  .modal-body {
    line-height: 1.7;
    color: $muted-text;
    font-size: 1.8rem;
    text-align: justify;
    padding: 10px 0;

    p {
      margin-bottom: 18px;
    }

    ul {
      margin: 15px 0;
      padding-left: 20px;

      li {
        margin-bottom: 8px;
        list-style: disc;
      }
    }
  }

  // ===== FOOTER / BOUTON DE RETOUR =====
  .button.return {
    @include modern-button($second-color, $second-color);
    margin-top: 30px;
    display: inline-block;
  }
}

// ==========================================
// 5. ANIMATIONS
// ==========================================
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.97);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

// ==========================================
// 6. EXTRA DESIGN TOUCHES
// ==========================================
.modal-content::before {
  content: "";
  position: absolute;

  inset: 0;
  border-radius: inherit;
  background: linear-gradient(
    145deg,
    rgba(255, 255, 255, 0.25),
    rgba(255, 255, 255, 0.05)
  );
  z-index: -1;
}

.modal-content:hover {
  box-shadow: $shadow-hover;
  transform: translateY(-4px) scale(1.01);
}

.radioDisplay {
  display: flex;
  gap: 20px;
  justify-content: center;
  margin-bottom: 30px;
  flex-wrap: wrap;

  .radioItem {
    label {
      color: $light-text;
      background: rgba(255, 255, 255, 0.1);
      padding: 8px 16px;
      border-radius: 30px;
      cursor: pointer;
      transition: $fast-transition;
      font-size: 0.95rem;
    }

    input[type="radio"] {
      display: none;

      &:checked + label {
        background: linear-gradient(135deg, $principal-color, $second-color);
        color: #fff;
        box-shadow: 0 4px 15px rgba($second-color, 0.4);
      }
    }
  }
  
}

