.languages {

    h2 {
        text-align: center;

    }

    .content {
        display: flex;

        justify-content: space-between;

        .languagesSection,
        .frameworksSection {
            width: 50%;
            margin: 10px;
            padding: 5px;
            border-radius: 15px;

            &:hover {
                box-shadow: 0 5px 0 5px $principal_color;
            }
        }

    }


    @media screen and (max-width: 850px) {
        .content {
            width: 100%;
            flex-direction: column;
            justify-content: center;

            .frameworksSection,
            .languagesSection {
                width: 100%;
            
            }
        }

    }



}